import "./index.scss";
import axios from "../../utils/axios.js"
import {setComment} from "../../actions";
import {addLike} from "../../actions";
import {connect} from "react-redux"

@connect(state=>{
    return{
        ...state
    }
})

export class Comment extends Component{
    componentWillMount(){
        let id =location.href.split("?")[1].split("=")[1]
        this.props.dispatch(setComment(id))
    }
    like=(id,index)=>{
        axios.get("/setLike",{params:{id}}).then(res=>{
           this.props.dispatch(addLike(index))
        })
    }
    render(){
        return(
            <div className="comment">
                <ul>
                    {
                        this.props.home.comment&&this.props.home.comment.map((item,index)=>{
                            return(
                                    <li key={index}>
                                        <div className="left">
                                            <img src={require("../../../assets/images/swiper2.jpg")} alt=""/>
                                            <div>
                                                <span>{item.usertel}</span>
                                                <span>{item.time}</span>
                                            </div>
                                        </div>
                                        <div className="right">
                                            <span className="iconfont icon-qinziAPPtubiao-1" onClick={()=>this.like(item["_id"],index)}></span>
                                            <span>{item.like}</span>
                                        </div>
                                        <p>
                                            {item.content}
                                        </p>
                                </li>
                            )
                        })
                    }
                </ul>
            </div>
        )
    }
}


