import "./index.scss";
import {connect} from "react-redux";
import {countAdd} from "../../actions";
import {countJ} from "../../actions";
import { Button,WhiteSpace,PullToRefresh} from 'antd-mobile';
import {Head} from "../../components/head";
import {Swiper} from "../../components/swiper";
import {Section} from "../../components/section";
import {Every} from "../../components/every";
import {Special} from "../../components/special";
import {getCartoons} from "../../actions";
import {Bottom} from "../../components/bottom";
import {ToTop} from "../../components/toTop";
@connect(
    (state)=>{
        return{
            ...state
        }
    }
)
  

export class Home extends Component{
    constructor(){
        super();
        this.state={
            flag:false,
            refreshing: false,
            down: true,
            height: document.documentElement.clientHeight-50,
            data: [],

        }
    }
    componentWillMount(){
        this.props.dispatch(getCartoons({
            url:"/cartoons",
            cb(){
                console.log("数据请求成功")
            }
        }))
        document.addEventListener('scroll', this.scrollHandler);
    }
    componentWillUnmount(){
        document.removeEventListener('scroll',this.scrollHandler)
    }
    scrollHandler=(e)=>{
      if(document.documentElement.scrollTop>350){
          this.setState({flag:true})
      }else{
        this.setState({flag:false})
      }
    }
   
    data=(v)=>{
         let list=this.props.home.cartoons.filter((item)=>{
            return item.tags.indexOf(v)!=-1
            })
            list.length=6;
            return list
    }


    render(){
        const {
            dispatch,
            history
            }=this.props
        return(
            <div>
                <Head history={history}></Head>
                <PullToRefresh
                    damping={60}
                    ref={el => this.ptr = el}
                    style={{
                    height: this.state.height,
                    overflow: 'auto',
                    }}
                    indicator={this.state.down ? {} : { deactivate: '上拉可以刷新' }}
                    direction={this.state.down ? 'down' : 'up'}
                    refreshing={this.state.refreshing}
                    onRefresh={() => {
                        this.setState({ refreshing: true });
                        this.props.dispatch(getCartoons({
                            url:"/cartoons",
                            cb(){
                                console.log("数据请求成功")
                            }
                        }))
                    setTimeout(() => {
                        this.setState({ refreshing: false });
                    }, 1000);
                    }}
                >
                
                <Swiper></Swiper>
                <Section  data={this.data("浪漫")} history={history}></Section>
                <Every></Every>
                <Special></Special>
                <Section  data={this.data("冒险")} cla="classic" history={history}></Section>
                <Section  data={this.data("校园")} cla="new" history={history}></Section>
                <Bottom></Bottom>
                <ToTop isshow={this.state.flag}></ToTop>

                
       
                </PullToRefresh>
            </div>
        )
    }

}


