import "./index.scss";
import axios from "../../utils/axios.js";
import { connect } from "react-redux";
import {insertComment} from "../../actions";
import { Modal, Toast } from 'antd-mobile';
const alert = Modal.alert;
@connect(
    state=>{
        return{
            ...state
        }
    }
)

export class CommentFoot extends Component{
    submit=()=>{
        let content=this.refs.content.value
        let usertel = localStorage.usertel
        let id =location.href.split("?")[1].split("=")[1]
        if(!usertel){
            alert('提示', '您还没登陆是否去登陆???', [
                { text: '取消', onPress: () => console.log('取消') },
                { text: '确定', onPress: () => this.props.history.push("/app/login") },
            ])
            return
        }
        axios.get("/subComent",{params:{id,usertel,content}}).then(res=>{
            if(res.data.code==1){
              this.props.dispatch(insertComment(res.data.data))
              Toast.success('评论成功', 1);
              console.log(this.props.home)
            }
        })
    }
    render(){
        return(
            <div className="worp">
            <div className="comment-foot" style={{display:this.props.show?"none":"block"}}>
                <div>
                    <input type="text"  placeholder="我也来说两句" ref="content" />
                </div>
                <button onClick={this.submit}>发表</button>
            </div>
            </div>
        )
    }
}


