import "./index.scss";
import {Link,navLink} from "react-router-dom";



import creatHistory from 'history/createHashHistory'     



export class Head extends Component{
    constructor(){
        super();
        this.state={
            display: "block",
            isShow:true,

        }
    }
    handleClick=(e)=>{
        this.setState({
            isShow:!this.state.isShow,
            display:this.state.isShow?"block":"block",
        })
        let list = document.getElementsByClassName("nav-list")[0];
        this.state.isShow?list.style.cssText="top:50px":list.style.cssText="-310px";
    }
    back=()=>{
        const history =creatHistory();

        history.goBack();
        
      
    }
    search=()=>{
        this.props.history.push("/app/search")
    }
    render(){
        let img = require("../../../assets/images/jlt.png")
        return(
            <div className="box-head" style={{height:50}}>
            <div className="nav" style={{height:50}}>
                <h1 className="logo" style={{width:102,background:"url("+img+") no-repeat -1px -56px",backgroundSize:"260px 150px",display:this.props.show?"none":"block"}}></h1>
                <div className="left-logo" style={{display:this.props.show?"block":"none"}}>
                    <div className="back" style={{width:"42px",height:"42px",marginTop:"4px",textAlign:"center"}}>
                        <span className="iconfont icon-back"  onClick={this.back}  style={{lineHeight:"42px",fontSize:"28px",display:this.props.show?"block":"none"}}></span>
                    </div>
                    <div className="title" style={{height:"22px",fontSize:18,lineHeight:"22px",marginTop:14,marginLeft:"4px",fontWeight:"bold"}}>
                        {this.props.title}
                    </div>
                </div>
                <div className="menu" style={{width:50,height:50}} onClick={this.handleClick}>
                    <span className="iconfont icon-gengduo" style={
                        {width:50,
                        height:50,
                        fontSize: "25px",
                        textAlign: "center",
                        lineHeight: "50px"}
                    }
                  
                    ></span>
                </div>
                <div className="search">
                    <span className="iconfont icon-sousuo" style={
                        {width:50,
                        height:50,
                        fontSize: "25px",
                        textAlign: "center",
                        lineHeight: "50px"}
                    }
                    onClick={this.search}
                    ></span>
                </div>
            </div>
            <ul className="nav-list">
            <li>
                <Link to="home">
                <div>
                   
                    <i className="iconfont icon-shouye"></i>
                    <span>首页</span>
                   
                </div>
                </Link>
            </li>
            <li> 
                <Link to="collection">
                    <div>
                        
                        <i className="iconfont icon-shoucang"></i>
                        <span>收藏</span>
                    
                    </div>
                </Link>
            </li>
            <li>
                <Link to="collection">
                    <div>
                    <i className="iconfont icon-lishi"></i>
                        <span>历史</span>
                    </div>
                </Link>
            </li>
            <li>
                <Link to="search">
                <div>
                <i className="iconfont icon-fenlei"></i>
                    <span>分类</span>
                </div>
                </Link>
            </li>
            <li>
            <Link to="search">
                <div>
                <i className="iconfont icon-paihangbang"></i>
                    <span>排行</span>
                </div>
                </Link>
            </li>
            <li>
                <Link to="search">
                <div>
                <i className="iconfont icon-shizhong"></i>
                    <span>追更</span>
                </div>
                </Link>
            </li>
            <Link to="collection" >
            <li className="btn-home" >
                    我的地盘
            </li>
            </Link>
        </ul>
        </div>
        )
    }
}



