import "./index.scss";

export class ToTop extends Component{
    handleClick=()=>{
        window.scrollTo({
            left: 0,
            top: 0,
            //动画
            behavior: 'smooth'
          })
    }
    render(){
        return(
            <div className="to-top" onClick={this.handleClick} style={{display:this.props.isshow?"block":"none"}}>
                <span className="iconfont icon-fanhuidingbu">
                </span>
            </div>
        )
    }
}


