import "./index.scss";
import { NavBar, Icon ,Toast} from 'antd-mobile';
import axios from "../../utils/axios.js"
export class Login extends Component{
    log=()=>{
        let usertel = this.refs.tel.value
        let password = this.refs.pwd.value
        let telFlag = (/^[13458][0-9]{10}$/).test(usertel)
        let pwdFlag = (/[0-9a-zA-Z]$/).test(password)
        if(!telFlag)Toast.fail('请输入正确手机号', 1);
        if(!pwdFlag)Toast.fail('请输入正确密码', 1);
        if(telFlag&&pwdFlag){
            axios.post("/login",{usertel,password}).then(res=>{
                if(res.data.code==1){
                 Toast.success('登陆成功', 1);
                    localStorage.usertel=usertel;
                    setTimeout(()=>{
                        this.props.history.push("/app/home")
                    },1500)
                   
                }else{
                     Toast.fail('用户名或密码错误', 1);
                }
             })
        }
        
      
    }
    render(){
        return(
            <div>
                <NavBar
      mode="light"
      icon={<Icon type="left" />}
      onLeftClick={() =>this.props.history.go(-1) }
     
    >登陆</NavBar>
            <div className="login">
                <img src={require("../../../assets/images/qq.png")} alt=""/>
                <div className="web_login">
                    <input type="tel" ref="tel"  placeholder="手机号"   />
                    <input type="password" ref="pwd"  placeholder="密码" />
                    <button onClick={this.log}>登陆</button>
                    <button onClick={()=>this.props.history.push("register")}>注册</button>
                </div>
            </div>
            </div>
        )
    }
}


