import "./index.scss";
import {Recommend} from "../recommend"

export class Directory extends Component{
    render(){
        const li =[]
        for(var i=168;i>168-9;i--){
            li.push(<li key={i}>{i}</li>)
        }
        return(
            <div className="directory">
                <p>2019-03-29 更新到168话</p>
                <ul className="list">
                    {li}
                </ul>
                <h3>大人，查看跟多目录</h3>
                <Recommend></Recommend>
            </div>
        )
    }
    
}


