import  axios from "./axios"

export const addHistory=(usertel,id,time,cb)=>{
    axios.get("/addHistory",{params:{usertel,id,time}}).then(res=>{
       
        cb(res)
    },
    err=>{
        cb(err)
    })
}

export const addHeat = (id,cb)=>{
    axios.post("/addHeat",{id}).then(res=>{
        cb(res)
    },
    err=>{
        cb(err)
    })
}


