import "./index.scss";
import {Title} from "../title";
import axios from "../../utils/axios"
import {connect} from "react-redux"
import {addHistory,addHeat} from "../../utils"

export class Section extends Component{
    gotuinfo=(id)=>{
        addHeat(id,(data)=>{

        })
      
       let time = new Date();
       //添加历史纪录
       addHistory(localStorage.usertel,id,time,(data)=>{

       })
       this.props.history.push("/app/info?id="+id)
    }

    render(){
        return(
            <div>
                <Title cla={this.props.cla} history={this.props.history}></Title>
                <ul className="cartoons">
                    {
                        this.props.data&&this.props.data.map((item,index)=>{
                            return (
                                <li key={index} onClick={()=>this.gotuinfo(item.id)} className="rb">
                                    <img src={item["cover_url"]} alt=""/>
                                    <h4>{item.title}</h4>
                                    <p>{item["short_desc"]}</p>
                                </li>
                            )
                        })
                    }
                 </ul>
            </div>
        )
    }
}


