
import ReactDOM,{render} from "react-dom";

import {IndexView} from "./views";

import store from "./store";
import {Provider} from "react-redux";

const hotRender = (Cpt)=>{
    render(
        <Provider store={store}>
        <Cpt/>
        </Provider>,
          document.getElementById("app")
    )
}
hotRender(IndexView);



