import "./index.scss";
import { NavBar, Icon,Toast } from 'antd-mobile';
import axios from "../../utils/axios.js"
export class Register extends Component{
    reg=()=>{
        let usertel = this.refs.tel.value
        let  password = this.refs.pwd.value
        let telFlag = (/^[13458][0-9]{10}$/).test(usertel)
        let pwdFlag = (/[0-9a-zA-Z]$/).test(password)
        if(!telFlag)Toast.fail('请输入正确手机号', 1);
        if(!pwdFlag)Toast.fail('请输入正确密码', 1);
        if(telFlag&&pwdFlag){
            axios.post("/register",{usertel,password}).then(res=>{
                if(res.data.code==1){
                    Toast.success('注册成功', 1);
                    setTimeout(()=>{
                        this.props.history.push("/app/login")
                    },1500)
                }else{
                    Toast.fail('手机号已注册', 1);
                }
            })
        }
    }
    render(){
        return(
            <div>
                <NavBar
      mode="light"
      icon={<Icon type="left" />}
      onLeftClick={() =>this.props.history.go(-1) }
    >注册</NavBar>
            <div className="register">
                <img src={require("../../../assets/images/qq.png")} alt=""/>
                <div className="web_login">
                    <input type="tel"  placeholder="手机号" ref="tel" />
                    <input type="password" placeholder="密码" ref="pwd"/>
                    <button onClick={this.reg}>注册</button>
                    <button onClick={()=>this.props.history.push("login")}>登陆</button>
                </div>
            </div>
            </div>
        )
    }
}


