import { Tabs, WhiteSpace } from 'antd-mobile';
import { StickyContainer, Sticky } from 'react-sticky';
import "./index.scss"
import {Recommend} from "../../components/recommend";
import {Directory} from "../../components/directory";
import {Comment} from "../../components/comment"
function renderTabBar(props) {

  return (<Sticky>
    {({ style }) => <div style={{ ...style, zIndex: 1 }}><Tabs.DefaultTabBar {...props} /></div>}
  </Sticky>);
}
const tabs = [
  { title: '详情' },
  { title: '目录' },
  { title: '评论' },
];
export const TabExample = (props) => (
  
 
  <div>
    <WhiteSpace />
    <StickyContainer>
      <Tabs tabs={tabs}
        initalPage={'t2'}
        renderTabBar={renderTabBar}
        onChange={(title)=>{
          props.handle(title.title)
        }}
      >
        <div className="detail">
          <h4 className="iconfont icon-xiangxi"></h4>
          <p>
          武之巅峰，移山填海，捉星拿月，逆乱时空！剑之极境，一剑成道，一剑通神，一剑破万法！少年李浮尘，偶得金色小符，灵魂不断进化，在这天才如雨，强者如云的世界，以手中之剑，登临绝巅。改编自网易文学作者剑游太墟同名小说《永恒至尊》永恒至尊永恒至尊QQ一号群：224256018 (已满)，QQ二号群：605470435 (欢迎)   欢
          </p>
          <Recommend></Recommend>
      
        </div>
        <div>
          <Directory></Directory>
         
        </div>
        <div>
          <Comment></Comment>
        </div>
      </Tabs>
    </StickyContainer>
    <WhiteSpace />
  </div>
);




