import {COUNTADD} from "../actions";
import {COUNTJ} from "../actions";
import {GETCARTOONS} from "../actions";
import {INSERTCOMMENT} from "../actions";
import {SETCOMMENT,ADDLIKE,ADDCOLLECTION,GETCOLLECTION,REVOVECOLLECTION} from "../actions";


const defaultState={
    count:0,
    cartoons:[],
    comment:[],
    collection:[]
}
export default (state=defaultState,action)=>{
    switch(action.type){
        case COUNTADD:
            state.count++
            return {...state}
        case COUNTJ:
            state.count--;
            return {...state}
        case GETCARTOONS:
            state.cartoons=action.result
            return {...state}
        case SETCOMMENT:
            state.comment=action.result
            return {...state}
        case INSERTCOMMENT:
            if(action.data[0]){
                state.comment.unshift(action.data[0])
            }
            return {...state}
        case ADDLIKE:
            state.comment.forEach((item,index)=>{
                if(index==action.index){
                    state.comment[index].like++
                }
            })
            return {...state}
        case ADDCOLLECTION:
            if(action.result){
                state.collection.push(action.result)
            }
            return {...state}
        case GETCOLLECTION:
            if(action.result){
                state.collection=action.result
            }
            return {...state}
        case REVOVECOLLECTION:
            if(action.result){
                state.collection=state.collection.filter(item=>{
                    return  item.id==action.result.id
                })
            }
            return {...state}

        default:
            return state;
    }
}




