import { Carousel, WingBlank } from 'antd-mobile';
import "./index.scss";
import {Link} from "react-router-dom";
export class Swiper extends React.Component {
  state = {
    data: ['0', '1', '2',"3","4"],
    imgHeight: 176,
    imgUrl:"../../../assets/images/swiper"
  }
  render() {
    return (
        <div id="swiper">
        <Carousel
          autoplay={true}
          infinite
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="#"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={require(`../../../assets/images/swiper${val}.jpg`)}
                alt=""
                style={{ width: '100%', verticalAlign: 'top',touchAction: "pan-y"}}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
                
              />
            </a>
          ))}
        </Carousel>
        <ul className="menu-list">
            <li>
              <Link to="search">
                <div>
                    <i className="iconfont icon-fenlei"></i>
                    <span>分类</span>
                </div>
                </Link>
            </li>
            <li>
            <Link to="search">
                <div>
                    <i className="iconfont icon-paihangbang"></i>
                    <span>排行</span>
                </div>
                </Link>
            </li>
            <li>
            <Link to="collection">
                <div>
                    <i className="iconfont icon-lishi"></i>
                    <span>追更</span>
                </div>
                </Link>
            </li>
            <li>
            <Link to="collection">
                <div>
                    <i className="iconfont icon-lishijilu"></i>
                    <span>历史</span>
                </div>
                </Link>
            </li>
        </ul>
        </div>
    )
  }
}


