import "./index.scss";

export class Guide extends Component{
    
    componentDidMount(){
        if(localStorage.appCount){
            localStorage.appCount++;
            if(localStorage.appCount>3){
                this.props.history.push("/app")
            }
        }else{
            localStorage.appCount=1;
        }
    }

    render(){
        return(
            <div className="box">
            
          
                <video id="video" src="http://106.13.110.12/tengxunmanhua/guide.mp4"  autoPlay  muted ></video>
            </div>
        )
    }
}


