import "./index.scss";
import { SearchBar, Button, WhiteSpace, WingBlank ,Toast,ListView} from 'antd-mobile';
import axios from "../../utils/axios"
// import ListContainer from "../../components/xlsx"
import { PullToRefresh} from 'antd-mobile';
// import {Xlsx} from "../../components/xlsx";
import {addHistory,addHeat} from "../../utils"

export class Search extends Component{
    constructor(){
        super();
        this.state={
            list:[],
            count:0,
            word:"",
            refreshing: true,
            down: true,
            height: document.documentElement.clientHeight,
        }
    }




    componentDidMount(){
        this.getmanhua("");
    }
    getmanhua=(keyword)=>{
        axios.get("/getmanhua",{params:{keyword,count:this.state.count}}).then(res=>{
            res.data.sort(()=>{
                return Math.random()-0.5
            }).length=10
            this.setState({list:res.data})
        })
    }

    loadingToast=()=> {
        Toast.loading('Loading...', 1, () => {
          console.log('Load complete !!!');
        });
      }
      keyword=(val)=>{
        this.setState({word:val})
        this.getmanhua(val);
      }
      search=()=>{
        this.getmanhua(this.state.word);
        this.setState({word:""})
      }
      back=()=>{
            this.props.history.goBack()
      }
      addh=(id)=>{
        let time = new Date();
        //添加历史纪录
        addHistory(localStorage.usertel,id,time,(data)=>{

        })
        addHeat(id,(data)=>{
            
        })
        this.props.history.push("/app/info?id="+id)
      }
    
    render(){
       
        return(
            <div>
                <div className="search-box">
                    <div className="top">
                        <div className="back iconfont icon-back" onClick={this.back}>
                        </div>
                        <div className="search">
                            <SearchBar placeholder="请输入关键字" onChange={(val)=>this.keyword(val)}  ref={ref => this.autoFocusInst = ref} />
                            <div className="search-list" style={{display:this.state.word?"block":"none"}} onClick={this.search}>
                                {this.state.word}
                            </div>
                        </div>
                    </div>
                </div>
                <ul className="sea-list">

                <PullToRefresh
                    damping={60}
                    ref={el => this.ptr = el}
                    style={{
                    height: this.state.height-55,
                    overflow: 'auto',
                    }}
                    indicator={this.state.down ? {} : { deactivate: '上拉可以刷新' }}
                    direction={this.state.down ? 'down' : 'up'}
                    refreshing={this.state.refreshing}
                    onRefresh={() => {
                    this.setState({ refreshing: true });
                    this.getmanhua("");
                    setTimeout(() => {
                        this.setState({ refreshing: false });
                    }, 1000);
                    }}
                >
                    {
                        this.state.list&&this.state.list.map((item,index)=>{
                            return(
                                    <li key={index}  onClick={()=>this.addh(item.id)} className="rb">
                                        <img src={item["cover_url"]} alt=""/>
                                        <div>
                                            <h3>{item.title}</h3>
                                            <p>2019-04-08更新</p>
                                            <p>{item.tags}</p>
                                            <p>{item["short_desc"]}</p>
                                        </div>
                                    </li>
                            )
                        })
                    }
                   
                </PullToRefresh>
                
               





                    
                </ul>
            </div>
        )
    }
}


