
import axios from "../utils/axios"

export const COUNTADD = "countAdd";
export const countAdd =()=>{
    return{
        type:COUNTADD
    }
}

export const COUNTJ = "countJ";
export const countJ =()=>{
    return {
            type:COUNTJ
    }
}

export const GETCARTOONS = "getCartoons";
export const getCartoons =({url,cb})=>{
    return axios.get(url).then(res=>{
        cb();
        let result = res.data.sort(()=>{
            return Math.random()-0.5
        })
        
        return{
            type:GETCARTOONS,
            result
        }
    })
}

export const INSERTCOMMENT = "insertComment";
export const insertComment =(data)=>{
    return {
            type:INSERTCOMMENT,
            data
    }
}

export const SETCOMMENT = "setComment";
export const setComment =(data)=>{
   
    return  axios.get("/comment",{params:{id:data}}).then(res=>{
        console.log(res.data)
            return{
                type:SETCOMMENT,
                result:res.data
            }
        })
}
export const ADDLIKE = "addLike";
export const addLike =(index)=>{
    return {
            type:ADDLIKE,
            index
    }
}

export const ADDCOLLECTION = "addCollection";
export const addCollection =(id,usertel)=>{
    return  axios.get("/addCollection",{params:{id,usertel}}).then(res=>{
        console.log(res.data)
            return{
                type:ADDCOLLECTION,
                result:res.data.data[0]
            }
        })
}

export const REVOVECOLLECTION = "removeCollection";
export const removeCollection =(id,usertel)=>{
    return  axios.get("/removeCollection",{params:{id,usertel}}).then(res=>{
        console.log(res.data)
            return{
                type:REVOVECOLLECTION,
                result:res.data
            }
        })
}


//获取用户收藏
export const GETCOLLECTION = "getCollection";
export const getCollection =(usertel)=>{
    return  axios.get("/getCollection",{params:{usertel}}).then(res=>{
        console.log(res.data)
            return{
                type:GETCOLLECTION,
                result:res.data
            }
        })
}





