import "./index.scss";
import {Title} from"../../components/title"

export class Special extends Component{
    render(){
        return(
            <div className="special">
                <Title cla="special" isshow="false"></Title>
                <div>
                    <img src={require("../../../assets/images/promotion-topics.jpg")} alt=""/>
                    <img src={require("../../../assets/images/promotion-vip.jpg")} alt=""/>
                </div>
            </div>
        )
    }
}


