import "./index.scss";
import {Head} from "../../components/head";
import { Badge } from 'antd-mobile';
import {TabExample} from "../../components/tabBar";
import {Foot} from "../../components/foot";
import {CommentFoot} from "../../components/commentFoot";
import axios from "../../utils/axios.js"

export class Info extends Component{
    constructor(){
        super();
        this.state={
            word:"",
            data:{}
        }
    }
    handle=(val)=>{
        
       setTimeout(()=>{
        this.setState({word:val})
       },0)
    }
    componentDidMount(){
        let id =this.props.location.search.split("=")[1];
       
        axios.get("/show",{ params:{id}}).then(res=>{
           
           if(res.data[0]){
            this.setState({data:res.data[0]})
           }else{
               alert("网络错误")
           }
         
        })
    }
    render(){
        if(this.state.data){
            var html = (
                    <ul className="head-info">
                        <li className="img">
                            <img src={this.state.data["cover_url"]} alt=""/>
                        </li>
                        <li>
                            <div>
                                <h3 className="title">
                                {this.state.data.title}
                                </h3>
                                <Badge text="8.2" hot style={{ marginLeft: 12 }} />
                            </div>
                            <p>标签：{this.state.data.tags}</p>
                            <p>作者：风人动漫</p>
                            <p>人气：<span>{this.state.data.heat}</span></p>
                            <p>月票：<span>11</span></p>
                        </li>
                    </ul>
               )
        }
        return(
            <div className="info">
              <Head title={this.state.data.title} show="true" history={this.props.history}></Head>
              {html}
              <TabExample  handle={this.handle} ></TabExample>
                <Foot show={this.state.word=="评论"?true:false}  history={this.props.history}></Foot>
                <CommentFoot show={this.state.word=="评论"?false:true} history={this.props.history}></CommentFoot>
              
            </div>
        )
    }
}


