import "./index.scss";
import {connect} from "react-redux"
import {addCollection,getCollection,removeCollection} from "../../actions";
import { Modal, Toast } from 'antd-mobile';
const alert = Modal.alert;
@connect(state=>{
    return{
        ...state
    }
})


export class Foot extends Component{
    componentDidMount(){
        this.props.dispatch(getCollection(localStorage.usertel))
    }
    collection=()=>{
        let id=location.href.split("?")[1].split("=")[1];
        let usertel = localStorage.usertel;
        if(!usertel){
           alert('提示', '您还没登陆是否去登陆???', [
                { text: '取消', onPress: () => console.log('取消') },
                { text: '确定', onPress: () => this.props.history.push("/app/login") },
            ])
            return
        }
        var flag = false
        if(this.props.home.collection){
            flag=this.props.home.collection.some(item=>{
                return item.id==location.href.split("?")[1].split("=")[1]
            })
        }
        if(flag){
            this.props.dispatch(removeCollection(id,usertel))
            Toast.success('取消收藏', 1);
           
        }else{
            this.props.dispatch(addCollection(id,usertel))
            Toast.success('添加收藏', 1);
        }

       
    }

    render(){
        var flag = false
        if(this.props.home.collection){
           flag=this.props.home.collection.some(item=>{
                return item.id==location.href.split("?")[1].split("=")[1]
            })
        }
        return(
            <div className="box " style={{display:this.props.show?"none":"block"}}>
                <div className="foot">
                    <ul className="left">
                        <li className="iconfont icon-shoucang1" onClick={this.collection} style={{color:flag?"red":""}}></li>
                        <li className="iconfont icon-fenxiang-copy"></li>
                        <li className="iconfont icon-xiazai"></li>
                    </ul>
                    <div className="right">
                        开始阅读
                    </div>
                </div>
            </div>
        )
    }
}


