import "./index.scss";
import {Head} from "../../components/head"
import axios from "../../utils/axios"
import {connect} from "react-redux";
import { Toast} from 'antd-mobile';
@connect(
    state=>{
        return{
            ...state
        }
    }
)

export class Collection extends Component{
    constructor(){
        super();
        this.state={
            show:true,
            list:[],
            historyList:[]
        }
    }
    componentDidMount(){
        if(!localStorage.usertel){
            Toast.offline("您还未登陆！", 1);
           setTimeout(()=>{
                this.props.history.push("/app/login")
           },2000)
        }else{
            let usertel = localStorage.usertel;
            axios.get("/getCollections",{params:{usertel}}).then(res=>{
                this.setState({list:res.data})
            })
            axios.get("/getHistory",{params:{usertel}}).then(res=>{
                this.setState({historyList:res.data})
            })
        }
       
    }

    render(){
        return(
            <div>
                 <Head title="个人中心" show="true" history={this.props.history}></Head>
                <ul className="tab-list">
                    <li onClick={()=>this.setState({show:true})} style={{borderBottomColor:this.state.show?"#ff9a66":""}}>收藏</li>
                    <li onClick={()=>this.setState({show:false})}  style={{borderBottomColor:this.state.show?"":"#ff9a66"}}>历史</li>
                </ul>
                <div className="collection" style={{display:this.state.show?"block":"none"}} >
                    <ul className="sea-list">
                        {
                            this.state.list&&this.state.list.map((item,index)=>{
                                return(
                                        <li key={index} onClick={()=>this.props.history.push("/app/info?id="+item.orders.id)} className="rb">
                                            <img src={item.orders["cover_url"]} alt=""/>
                                            <div>
                                                <h3>{item.orders.title}</h3>
                                                <p>2019-04-08更新</p>
                                                <p>{item.orders.tags}</p>
                                                <p>{item.orders["short_desc"]}</p>
                                            </div>
                                        </li>
                                )
                            })
                        }
                    </ul>
                </div>
                <div className="history" style={{display:this.state.show?"none":"block"}} >
                    <ul className="sea-list">
                            {
                                this.state.historyList&&this.state.historyList.map((item,index)=>{
                                    return(
                                            <li key={index} onClick={()=>this.props.history.push("/app/info?id="+item.orders.id)} className="rb">
                                                <img src={item.orders["cover_url"]} alt=""/>
                                                <div>
                                                    <h3>{item.orders.title}</h3>
                                                    <p>{item.orders.tags}</p>
                                                    <p>{item.orders["short_desc"]}</p>
                                                    <p>阅读时间：{item.time}</p>
                                                </div>
                                            </li>
                                    )
                                })
                            }
                        </ul>
                    </div>
            </div>
        )
    }
}


