import "./index.scss";
export class Title extends Component{
    render(){
        return(
            <div>
                <div className="tltle">
                    <h3 className={this.props.cla}></h3>
                    <span className="more" style={{display:this.props.isshow?"none":"block"}} onClick={()=>this.props.history.push("search")}></span>
                </div>
            </div>
        )
    }
}


